import styled from "styled-components";

// interface INav {
//   open: boolean;
// }

export const StyledBurger = styled.div`
  width: 2rem;
  height: 2rem;
  position: fixed;
  top: 15px;
  right: 20px;
  z-index: 20;
  display: none;
  cursor: pointer;
  font-family: "Zilla Slab", sans-serif;

  @media (max-width: 768px) {
    display: flex;
    justify-content: space-around;
    flex-flow: column nowrap;
  }
`;

export const Menus = styled.div`
  width: 2rem;
  height: 0.25rem;
  background-color: #000;
  border-radius: 10px;
  transform-origin: 1px;
  transition: all 0.3s linear;
  cursor: pointer;
  background: #ffa600;

  &:nth-child(1) {
    transform: ${(props) => (props.open ? "rotate(45deg)" : "rotate(0)")};
  }
  &:nth-child(2) {
    transform: ${(props) =>
      props.open ? "translateX(100%)" : "translateX(0)"};
    opacity: ${(props) => (props.open ? 0 : 1)};
  }
  &:nth-child(3) {
    transform: ${(props) => (props.open ? "rotate(-45deg)" : "rotate(0)")};
  }
`;

export const Nav = styled.nav`
  position: relative;
  font-family: "Zilla Slab", sans-serif;
  height: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #fdfdfdfa;
  align-items: center;
  background-color: blue;
  background: #28293d;

  @media (max-width: 678px) {
    width: 100vw;
  }
`;

export const Ul = styled.ul`
  font-family: "Zilla Slab", sans-serif;
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  gap: 10px;
  position: absolute;
  top: 0;
  justify-content: flex-end;
  margin-top: 0px;
  align-items: center;
  font-size: 18px;
  height: 70px;
  margin-left: 0px;
  width: fit-content;
  right: 5rem;

  a {
    color: #ffa600;
    transition: all 0.3s linear;
    border: 1px solid #ffa600;
    border-radius: 5px;
    padding: 1px 0;
    text-decoration: none;
    text-transform: none;
    cursor: pointer;

    &:hover {
      color: white;
      border-color: white;
    }
  }

  @media (max-width: 768px) {
    flex-flow: column nowrap;
    background-color: #fdfdfdfa;
    position: fixed;
    transform: ${(props) =>
      props.open ? "translateX(0)" : "translateX(100%)"};
    top: -16px;
    right: 0;
    height: 105%;
    width: 320px;
    align-items: inherit;
    padding-top: 3.5rem;
    transition: transform 0.3s ease-in-out;
    z-index: 9;
    justify-content: normal;
  }
`;

export const Li = styled.li`
  padding: 3px 10px;
  outline: none;

  @media (max-width: 768px) {
    color: #000;
    margin-right: 34px;

    &:hover {
      color: #0dadea;
    }
  }
`;

export const Logo = styled.img`
  margin: 0px 110px;
  width: 160px;
  height: 70px;
  object-fit: contain;

  @media (max-width: 1250px) {
    margin: 0px 52px;
  }
`;

export const LogoUl = styled.img`
  display: none;
  border-radius: 8px;

  @media (max-width: 768px) {
    display: flex;
    width: 160px;
    object-fit: contain;
  }
`;

export const Icon = styled.div`
  width: 100vw;
  height: calc(100vh - 112px);
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const Image = styled.img`
  width: 150px;
  height: 150px;
  pointer-events: none;
  object-fit: contain;
  animation: Spin infinite 20s linear;
`;
