import * as S from "./styles";
import Logo from "../assets/logo.jpg";

import React, { useState } from "react";
import { useWeb3React } from "@web3-react/core";

import { useModal } from "../contexts/modalContext";
import config from "../contexts/modalConfig";
import metamask from "../components/icons/metamask.svg";
import { Button } from "react-bootstrap";

import { Link } from "react-router-dom";
import useAuth from "./../hooks/useAuth";

import { connectorLocalStorageKey } from "../config";

// type Props = {
//   open: boolean;
// };

function RightNav(props) {
  const { account } = useWeb3React();
  const { login, logout } = useAuth();

  const { setModalTitle, setModalBody, unSetModal } = useModal();
  const accountElipsis = account
    ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}`
    : null;
  const walletConnect = () => {
    if (account) {
      unSetModal();
      logout();
      window.localStorage.removeItem(connectorLocalStorageKey);
    } else {
      unSetModal();
      login();
      window.localStorage.setItem(connectorLocalStorageKey, "injected");
    }
  };
  const WalletButtons = () => {
    return (
      <div style={{ display: "flex", flexDirection: "column" }}>
        {config.map((entry, index) => (
          <button
            className="wallet-logo-button "
            key={entry.title}
            onClick={walletConnect}
          >
            <img src={metamask} alt="meta" style={{ width: "60px" }} />
            <div style={{fontFamily:'Nerkoone',color:"orange"}}>{entry.title}</div>
          </button>
        ))}
      </div>
    );
  };

  return (
    <S.Ul open={props.open}>
      <Link to="/" style={{ border: "none" }}>
        <S.LogoUl src={Logo} alt={"Gustavo Scarpim"} />
      </Link>

      <Link to="/projects">
        <S.Li>IDO PROJECTS</S.Li>
      </Link>
      <Link to="/staking">
        <S.Li>STAKING</S.Li>
      </Link>
      <Link to="/vesting">
        <S.Li>VESTING</S.Li>
      </Link>
      <Link to="/applyido">
        <S.Li>APPLY IDO</S.Li>
      </Link>
      <Link>
        {account ? (
          <li
            onClick={() => {
              setModalTitle("Logout");
              setModalBody(
                <div className="wallet-logout">
                  <h5>{account}</h5>
                  <button className="wallet-logout-button" onClick={walletConnect}>Logout</button>
                </div>
              );
            }}
            // className="connect-button"
          >
            <S.Li>{accountElipsis}</S.Li>
          </li>
        ) : (
          <li
            onClick={() => {
              setModalTitle("Connect Wallet");
              setModalBody(<WalletButtons />);
            }}
            // className="connect-button"
          >
            <S.Li>CONNECT</S.Li>
          </li>
        )}
      </Link>
    </S.Ul>
  );
}

export default RightNav;
