import React from "react";

import Header from "../components/Header";
import Background from "../components/Background";

export default function ApplyIDO() {
  return (
    <>
      <Header />
      <Background />
      <div
        style={{
          position: 'absolute',
          textAlign: "center",
          fontFamily: "Tradewinds",
          fontSize: "50px",
          left: "calc(50% - 150px)",
          top: "calc(40% - 0px)",
        }}
      >
        Coming Soon...
      </div>
    </>
  );
}
