export default function Header(props) {
  return (
    <>
      <S.Nav>
        <Link to="/">
          <S.Logo src={Logo} alt="Gustavo Scarpim" />
        </Link>
      </S.Nav>
      <Burger />
      {props.children}
    </>
  );
}
